function sliderCertificateMain() {
    var swiper = new Swiper('.certificate-main__card .swiper', {
        slidesPerView: 1,
        spaceBetween: 30,
        navigation: {
            nextEl: '.certificate-main__card .swiper-button-next',
            prevEl: '.certificate-main__card .swiper-button-prev',
        },
        pagination: {
            el: ".certificate-main__card .swiper-pagination",
            clickable: true,
        },
    })
}

function gallery() {
    var swiper = new Swiper(".article-gallery .mySwiper", {
        loop: true,
        spaceBetween: 13,
        slidesPerView: 7,
        freeMode: true,
        watchSlidesProgress: true,
        breakpoints: {
            320: {
                slidesPerView: 3,
            },
            769: {
                slidesPerView: 5,
            },
            993: {
                slidesPerView: 7,
            }
        }
    });
    var swiper2 = new Swiper(".article-gallery .mySwiper2", {
        loop: true,
        spaceBetween: 13,
        navigation: {
            nextEl: ".article-gallery .swiper-button-next",
            prevEl: ".article-gallery .swiper-button-prev",
        },
        pagination: {
            el: ".article-gallery .swiper-pagination",
            type: "progressbar",
        },
        thumbs: {
            swiper: swiper,
        },
    });
}

function sliderProducts() {
    var swiper = new Swiper('.swiper-main', {
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
            nextEl: '.products .swiper-button-next',
            prevEl: '.products .swiper-button-prev',
        },
        pagination: {
            el: ".swiper-main .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            360: {
                slidesPerView: 1.3,
            },
            577: {
                slidesPerView: 2,
            },
            769: {
                slidesPerView: 3,
            }
        }
    })
}

let SlidersArray = []
function sliderProductsCard() {
    $(".product-card").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.product-card[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 10,
            observer: true,
            observeParents: true,
            pagination: {
                el: `.product-card[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
        })
    })
}

function sliderArticlesRelated() {
    var swiper = new Swiper('.articles-related .swiper', {
        slidesPerView: 4,
        spaceBetween: 30,
        navigation: {
            nextEl: '.articles-related .swiper-button-next',
            prevEl: '.articles-related .swiper-button-prev',
        },
        pagination: {
            el: ".articles-related .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 16,
            },
            360: {
                slidesPerView: 1.3,
                spaceBetween: 16,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 2.5,
                spaceBetween: 30,
            },
            782: {
                slidesPerView: 3,
                spaceBetween: 30,
            },
            993: {
                slidesPerView: 4,
                spaceBetween: 30,
            }
        }
    })
}

